import {movieAPI} from '../api/api'

const SET_MOVIE = 'SET_MOVIE'
const SET_MODAL = 'SET_MODAL'
const SET_TRAILERS = 'SET_TRAILERS'

export const setMovie = (movie, count, clb) => ({ type: SET_MOVIE, movie, count, clb })
export const setModal = (modal) => ({ type: SET_MODAL, modal })
export const setTrailers = (trailers) => ({ type: SET_TRAILERS, trailers })

export const getMovie = (currentPage, movie,clb) => async (dispatch) => {
   if (movie.length <= (20 * currentPage)){
   let response = await movieAPI.getMovie(currentPage)
   dispatch(setMovie(response.data.films, response.data.pagesCount,clb))
   }
}

export const getMovieModal = (filmId) => async (dispatch) => {
   let data = await movieAPI.getMovieModal(filmId)
   let resp = await movieAPI.getTrailer(filmId)
   dispatch(setModal(null))
   dispatch(setModal(data.data.data))
   dispatch(setTrailers(resp.data.trailers))
  
}



let initialState = {
   movie: [],
   trailers: [],
   modal: null,
   totalCount: null,
   currentPage: 1,
}

const movieReducer = (state = initialState, action) => {
   switch (action.type) {
      case SET_MOVIE: {
         action.clb && action.clb()
         return {
            ...state,
            movie: [...new Set([...state.movie, ...action.movie])],
            totalCount: action.count,
            currentPage: state.currentPage + 1
         }
      }
      case SET_MODAL: {
         return {
            ...state,
            modal: action.modal
         }
      }
      case SET_TRAILERS: {
         return {
            ...state,
            trailers: [...action.trailers
            .map(trailer => trailer.url)
            .filter(trailer => trailer.includes('https://www.youtube.com/watch?v=') || trailer.includes('https://youtu.be/'))
            .map(trailer => {
               if (trailer.includes('https://www.youtube.com/watch?v=')){
                  return trailer.replace('https://www.youtube.com/watch?v=', 'https://www.youtube.com/embed/')
               }
               else {
                  return trailer.replace('https://youtu.be/', 'https://www.youtube.com/embed/')
               }
            })
            ]
         }
      }
      default:
         return state;
   }
}

export default movieReducer
